.itemList{
    background-color: rgb(20, 238, 238);
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
    margin-left: 10%;
    width: 300px;
    text-align: center;
    padding: 5px;
    color: blueviolet;
    border-radius: 10px;
    box-shadow: 5px 10px 15px rgb(5, 68, 5);
}
.removeBtn{
    background-image:linear-gradient(to right, rgba(100,200,0), rgba(200,100,0));
    color: white;
    border: none;
    outline: none;
}
button:focus{
    border: none;
    outline: none;
}
