body{
    background-color: rgb(81, 105, 216);
    
}
.listContainer{
    background-color: seagreen;
    height: 500px;
    max-height: 70vh;
    width: 500px ;
    margin: 40px auto;
    padding: 10px;
}
form{
    margin-left: 20%;
    display: flex;
    justify-content: space-around;
    
}
input{
    border: none;
    outline: none;
    background-color: silver;
    padding: 10px;
    border-radius: 10px;
}
button{
    border: none;
    width: 70px;
    border-radius: 10px;
    color: rgb(94, 62, 184);
    background-color: tomato;
}